* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.loader {
  width: 64px;
  height: 64px;
  background: #fff;
  border-radius: 50%;
  display: none;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  background: #fff;
  border-radius: 50%;
  margin: auto;
  position: absolute;
  top: 8px;
  bottom: 8px;
  left: 8px;
  right: 8px;
}

.loader:before {
  content: "";
  background: #5f9ea0;
  margin: auto;
  animation: crlMugLoader 1s linear infinite alternate;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes crlMugLoader {
  0%, 10% {
    transform: translateY(64px);
  }

  90%, 100% {
    transform: translateY(0);
  }
}

