* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.loader {
  position: relative;
  width: 64px;
  height: 64px;
  background: #fff;
  border-radius: 50%;
  overflow: hidden;
  display: none;
}
.loader:after {
  content: '';
  position: absolute;
  inset: 8px;
  margin: auto;
  background: #ffffff;
  border-radius: 50%;
}
.loader:before {
  content: '';
  position: absolute;
  inset: 0px;
  margin: auto;
  background: cadetblue;
  animation: crlMugLoader 1s linear infinite alternate;
}
@keyframes crlMugLoader {
  0%,
  10% {
    transform: translateY(64px);
  }
  90%,
  100% {
    transform: translateY(0px);
  }
}
